import React from "react";
import {Modal} from "antd";

export const ModalWindow = ({isOpeningModal, changeVisibleModal}) => {
    return (
        <Modal visible={isOpeningModal} onOk={() => changeVisibleModal(false)} onCancel={() => changeVisibleModal(false)} title="Error message">
            <p>What's ways not plan!</p>
        </Modal>
    );
};