import {getIsOpeningModal} from "../../store/reselect/reselect";
import {connect} from "react-redux";
import {setIsOpeningModalActionCreator} from "../../store/reducers/modalReducer";
import {ModalWindow} from "./ModalWindow";

const mapStateToProps = state => {
    return {
        isOpeningModal: getIsOpeningModal(state)
    };
};

const mapDispatchToProps = dispatch => {
    return {
        changeVisibleModal: (isOpeningModal, error) => {
            dispatch(setIsOpeningModalActionCreator(isOpeningModal, error));
        }
    };
};

const ModalWindowContainer = connect(mapStateToProps, mapDispatchToProps)(ModalWindow);

export default ModalWindowContainer;